.report{
    margin: 50px 20px 0 20px;
    display: flex;
    flex-direction: column;
    padding:0;
    gap: 10px;
}

.div-one{
    width: 100%;
    min-height: 50%;
    display: flex;
    justify-content: space-between;
}


.area{
    margin: 10px 10px;
    padding: 10px;
    width: 30%;
    border-radius: 20px;
    

}

.one{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding:0;
}

.one > .report-card{
    width: 100%;
    height: 20%;
    margin: 10px 0;
    background-color: hsl(271, 76%, 53%, 0.3);
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    border-radius: 20px;
    display:flex;
    align-items: center;
    padding: 0 20px;
}

.report-card > .number{
    margin-left: auto;
}

.two{
    background-color: hsl(271, 76%, 53%, 0.3);
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
}

.two:hover , .report-card:hover{
    transform: scale(1.1);
}

.gau{
    display: flex;
    justify-content: center;
    align-items: center;
}

.data-delivery{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}




@media only screen and (max-width: 900px) and (min-width: 380px){
    .report{
        display: flex;
        flex-direction: column;
        padding:0;
        gap: 0px;
    }
    .div-one{
        width: 100%;
        min-height: 100%;
        margin: 0;
        /*margin-right: 200px;*/

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        gap: 10px
    }

    .div-one:last-child{
        margin-bottom: -200px;
    }

    .area{
        min-width: 90vw;
        min-height: 30vh;
        margin: 10px 10px;
        padding: 10px;
        border-radius: 20px;
    
    }
    
}